import React from "react";
import { Link, withRouter } from "react-router-dom";

import { addToCart, API_URL, shop, formatDate } from "../config/config";
import Api from "../config/Api";

import Header from "../components/Header";
import Footer from "../components/Footer";
import Loading from "../components/Loading";
import Categories from "../components/Categories";

import "../styles/article.css";

class Article extends React.Component {

    state = {
        offset: 0,

        article: null,

        loading: true
    }

    constructor() {
        super();

        this.loadArticle = this.loadArticle.bind(this);
    }

    async loadArticle() {
        this.setState({ loading: true });
            const id = this.props.match.params.articleId;

            const post = await Api.getPost(id);

            if (post.blog) {
                this.setState({ loading: false, article: post.blog });
            }
    }

    componentDidMount() {
        this.loadArticle()
    }

    render() {
        const article = this.state.article;

        return(
            <div className="screen" id="article">
                {!article ? <div className="content"><Loading /></div> : (
                    <div className="content">
                        <img className="image" src={API_URL + "/uploads/" + article.imagePath} />

                        <h3 className="title">{article.name}</h3>
                        <div className="date">{article.date ? formatDate(article.date) : null}</div>
                        <p className="text" style={{ marginBottom: 50 }}>{article.description}</p>

                        <p className="text" dangerouslySetInnerHTML={{ __html: article.html }} />
                    </div>
                )}
            </div>
        )
    }
}

export default withRouter(Article);